import React from 'react';
import styled from 'styled-components';
import { FaLinkedin, FaGithubSquare } from 'react-icons/fa';
import LineDiv from '../LineDiv/LineDiv';
import P from '../paragraph/P';

const StyledFooter = styled.footer`
  height: 100px;
  width: 100%;
  background-color: ${({ theme }) => theme.red.light};
  position: relative;
  padding-left: 150px;
  display: flex;

  @media ${({ theme }) => theme.device.mobileM} {
    height: 120px;
    padding-left: 300px;
  }

  @media ${({ theme }) => theme.device.tablet} {
    height: 200px;
    padding-left: 450px;
  }

  @media ${({ theme }) => theme.device.laptop} {
    height: 250px;
    padding-left: 550px;
  }

  @media ${({ theme }) => theme.device.laptopL} {
    padding-left: 750px;
  }

  @media ${({ theme }) => theme.device.desktop} {
    padding-left: 850px;
  }
`;

const StyledP = styled(P)`
  height: 50%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 100%;
  font-weight: 600;

  @media ${({ theme }) => theme.device.mobileM} {
    font-size: 2rem;
  }

  @media ${({ theme }) => theme.device.tablet} {
    font-size: 3rem;
  }

  @media ${({ theme }) => theme.device.laptop} {
    font-size: 3.5rem;
  }

  @media ${({ theme }) => theme.device.laptopL} {
    font-size: 4rem;
  }

  @media ${({ theme }) => theme.device.desktop} {
    font-size: 5rem;
  }
`;

const StyledA = styled.a`
  margin: 0;
  flex-basis: 50%;
  font-size: 6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: none;
  color: #fff;
  transition: background-color 0.5s ease;
  cursor: default;

  &:nth-of-type(1) {
    background-color: #0077b5;

    @media ${({ theme }) => theme.device.laptop} {
      &:hover {
        background-color: #42a7db;
      }
    }
  }

  &:nth-of-type(2) {
    background-color: #333;

    @media ${({ theme }) => theme.device.laptop} {
      &:hover {
        background-color: ${({ theme }) => theme.lightgray};
      }
    }
  }

  @media ${({ theme }) => theme.device.tablet} {
    font-size: 12rem;
  }

  @media ${({ theme }) => theme.device.laptop} {
    font-size: 15rem;
    cursor: pointer;
  }

  @media ${({ theme }) => theme.device.laptopL} {
    font-size: 15rem;
  }

  @media ${({ theme }) => theme.device.desktop} {
    font-size: 15rem;
  }
`;

const Footer = () => (
  <>
    <StyledFooter>
      <LineDiv>
        <StyledP>Made by Kacper</StyledP>
        <StyledP>kucza06@wp.pl</StyledP>
      </LineDiv>
      <StyledA
        href="https://www.linkedin.com/in/kacper-kuczewski-a50999187/"
        target="_blank"
      >
        <FaLinkedin />
      </StyledA>
      <StyledA href="https://github.com/superkacper4" target="_blank">
        <FaGithubSquare />
      </StyledA>
    </StyledFooter>
  </>
);

export default Footer;
