import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

const StyledPortfolioTile = styled.div`
  flex-basis: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: ${({ theme }) => theme.red.light};
  border: 5px solid ${({ theme }) => theme.red.strong};
  box-shadow: 10px 10px 20px black;
  padding: 10px;
  margin: 15px 0;
  z-index: 2;
  text-align: center;
  transform: ${({ active }) =>
    active ? 'scale(1) skew(0,0)' : 'scale(0.7) skew(5deg, 5deg)'};
  transition: transform 0.5s;

  @media ${({ theme }) => theme.device.mobileM} {
    flex-basis: 100%;
  }

  @media ${({ theme }) => theme.device.tablet} {
    flex-basis: calc(50% - 30px);
    margin: 15px 15px;
  }

  @media ${({ theme }) => theme.device.laptop} {
    flex-basis: calc(50% - 40px);
    margin: 15px 20px;
  }

  @media ${({ theme }) => theme.device.laptopL} {
    flex-basis: calc(100% / 3 - 40px);
    margin: 15px 20px;
  }

  @media ${({ theme }) => theme.device.desktop} {
    flex-basis: calc(100% / 3 - 70px);
    margin: 20px 35px;
  }
`;

class PortfolioTile extends React.Component {
  state = {
    active: false,
  };

  ref = React.createRef();

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll = () => {
    const { ref } = this;
    if (ref.current !== undefined && ref.current !== null) {
      const off = ref.current.getBoundingClientRect().top;
      if (off < window.innerHeight - 100) {
        this.setState({
          active: true,
        });
      }
    }
  };

  render() {
    const { active } = this.state;
    const { children } = this.props;
    const { ref } = this;
    return (
      <StyledPortfolioTile active={active} ref={ref}>
        {children}
      </StyledPortfolioTile>
    );
  }
}

PortfolioTile.propTypes = {
  children: PropTypes.element.isRequired,
};

export default PortfolioTile;
