const PortfolioPage = () => {
  const projectsArr = projects.map(project => (
    <PortfolioTile key={project.key}>
      {/* <StyledPortfolioTileImg src={project.img} /> */}
      <StyledPortfolioTileH3>{project.title}</StyledPortfolioTileH3>
      <P>{project.content}</P>
      <StyledPortfolioTileA
        href={project.link}
        target="_blank"
        rel="noopener noreferrer"
      >
        Go!
      </StyledPortfolioTileA>
    </PortfolioTile>
  ));
  return (
    <StyledPortfolioPage>
      <LineDiv />
      <StyledH2>Prace</StyledH2>
      <StyledWrapper>{projectsArr}</StyledWrapper>
    </StyledPortfolioPage>
  );
};

export default PortfolioPage;
