import styled from 'styled-components';

const P = styled.p`
  text-align: justify;
  margin: 0;
  padding: 10px 0;

  @media ${({ theme }) => theme.device.tablet} {
    font-size: 1.3rem;
  }

  @media ${({ theme }) => theme.device.laptop} {
    font-size: 1.8rem;
  }

  @media ${({ theme }) => theme.device.desktop} {
    font-size: 2rem;
  }
`;

export default P;
