import React from 'react';
import styled from 'styled-components';

import LineDiv from '../components/LineDiv/LineDiv';

const StyledStartPage = styled.div`
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
`;

const StyledH1 = styled.h1`
  padding: 0;
  margin: 0;
  font-size: 5rem;
  z-index: 2;
  text-align: center;
  animation: appearH1 2s ease-out both;

  @media ${({ theme }) => theme.device.mobileM} {
    font-size: 6rem;
  }

  @media ${({ theme }) => theme.device.tablet} {
    font-size: 7rem;
  }

  @media ${({ theme }) => theme.device.laptop} {
    font-size: 8rem;
  }

  @media ${({ theme }) => theme.device.laptopL} {
    font-size: 9rem;
  }

  @media ${({ theme }) => theme.device.desktop} {
    font-size: 10rem;
  }

  @keyframes appearH1 {
    0% {
      transform: scale(0.5) translateY(-200px);
      opacity: 0;
    }

    100% {
      transform: scale(1) translateY(0);
      opacity: 1;
    }
  }
`;

const StyledH2 = styled.h2`
  font-size: 3rem;
  z-index: 2;
  margin: 10px 0;
  animation: appearH2 1s 2.2s linear both;

  @media ${({ theme }) => theme.device.tablet} {
    font-size: 3.5rem;
  }

  @media ${({ theme }) => theme.device.laptop} {
    font-size: 4rem;
  }

  @media ${({ theme }) => theme.device.laptopL} {
    font-size: 4.5rem;
  }

  @media ${({ theme }) => theme.device.desktop} {
    font-size: 5rem;
  }

  @keyframes appearH2 {
    0% {
      /* transform: scale(0.5); */
      opacity: 0;
    }

    100% {
      /* transform: scale(1); */
      opacity: 1;
    }
  }
`;

const StyledDiv = styled(LineDiv)`
  background-color: ${({ theme }) => theme.red.light};
`;

const StartPage = () => (
  <StyledStartPage>
    <StyledDiv />
    <StyledH1>Kacper Kuczewski</StyledH1>
    <StyledH2>Zbi√≥r prac</StyledH2>
  </StyledStartPage>
);

export default StartPage;
